<template>
  <div class="allPage">
    <div class="core_container">
      <div class="showDataTab">
        <template>
          <br>
          <el-button type="success" size="mini" @click="addConfig"
            >新增</el-button
          >
        </template>
        <el-table
          :data="page.list"
          style="width: 100%;margin-top:5px;"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="app_id" label="AppId" width="90%">
          </el-table-column>
          <el-table-column prop="boss_id" label="BossId" width="90%">
          </el-table-column>
          <el-table-column prop="channel_code" label="渠道码" width="90%">
          </el-table-column>
          <el-table-column
            prop="account_config.gold"
            label="初始金币"
            width="90%"
          >
          </el-table-column>
          <el-table-column prop="app_version" label="App版本配置">
            <template slot-scope="scope">
              <span>上限：{{ scope.row.app_version.max }}</span
              ><br />
              <span>下限：{{ scope.row.app_version.min }}</span>
            </template>
          </el-table-column>
          <!-- <el-table-column prop="app_version.min" label="App版本配置下限值">
          </el-table-column> -->
          <el-table-column prop="lobby_version" label="大厅版本配置">
            <template slot-scope="scope">
              <span>上限：{{ scope.row.lobby_version.max }}</span
              ><br />
              <span>下限：{{ scope.row.lobby_version.min }}</span>
            </template>
          </el-table-column>
          <!-- <el-table-column prop="lobby_version.min" label="大厅版本配置下限值">
          </el-table-column> -->
          <el-table-column prop="app_link" label="App下载地址">
          </el-table-column>
          <el-table-column prop="game_link" label="子游戏下载地址">
          </el-table-column>
          <el-table-column prop="lobby_link" label="大厅资源下载地址">
          </el-table-column>
          <el-table-column prop="goods_ids" label="商品编号列表">
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                @click="handleEdit(scope.$index), (dialogFormVisible = true)"
                >编辑</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <template>
          <el-dialog
            :title="action.title"
            :visible.sync="dialogFormVisible"
            show-close:true
          >
            <el-form :model="config" ref="config">
              <el-form-item
                label="AppId"
                :label-width="formLabelWidth"
                prop="app_id"
                :required="true"
              >
                <el-input
                  v-model="config.app_id"
                  class="dialog_input"
                  :disabled="action.type == 'edit'"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="BossId"
                :label-width="formLabelWidth"
                prop="boss_id"
                :required="true"
              >
                <el-input
                  v-model="config.boss_id"
                  class="dialog_input"
                  :disabled="action.type == 'edit'"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="渠道码"
                :label-width="formLabelWidth"
                prop="channel_code"
                :required="true"
              >
                <el-input
                  v-model="config.channel_code"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="初始金币"
                :label-width="formLabelWidth"
                prop="account_config.gold"
                v-show="action.type == 'edit'"
              >
                <el-input
                  v-model="config.account_config.gold"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="App版本配置"
                :label-width="formLabelWidth"
                prop="app_version"
                v-show="action.type == 'edit'"
              >
                (高)
                <el-input
                  v-model="config.app_version.max"
                  class="dialog_input_small"
                ></el-input
                >-
                <el-input
                  v-model="config.app_version.min"
                  class="dialog_input_small"
                ></el-input
                >(低)
              </el-form-item>
              <el-form-item
                label="大厅版本配置"
                :label-width="formLabelWidth"
                prop="lobby_version"
                v-show="action.type == 'edit'"
              >
                (高)<el-input
                  v-model="config.lobby_version.max"
                  class="dialog_input_small"
                ></el-input>
                -
                <el-input
                  v-model="config.lobby_version.min"
                  class="dialog_input_small"
                ></el-input>
                (低)
              </el-form-item>
              <el-form-item
                label="App下载地址"
                :label-width="formLabelWidth"
                prop="app_link"
                v-show="action.type == 'edit'"
              >
                <el-input
                  v-model="config.app_link"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="子游戏下载地址"
                :label-width="formLabelWidth"
                prop="game_link"
                v-show="action.type == 'edit'"
              >
                <el-input
                  v-model="config.game_link"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="大厅资源下载地址"
                :label-width="formLabelWidth"
                prop="lobby_link"
                v-show="action.type == 'edit'"
              >
                <el-input
                  v-model="config.lobby_link"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="商品编号列表"
                :label-width="formLabelWidth"
                v-show="action.type == 'edit'"
              >
                <!-- <el-input
                  v-model="config.goods_ids"
                  class="dialog_input"
                ></el-input> -->
                <el-input
                  type="textarea"
                  :rows="2"
                  class="dialog_input"
                  placeholder="用逗号隔开，比如：123,457,457"
                  v-model="arraystr.goods_ids"
                >
                </el-input>
              </el-form-item>
              <el-form-item
                label="支持区号列表"
                :label-width="formLabelWidth"
                v-show="action.type == 'edit'"
              >
                <el-input
                  type="textarea"
                  :rows="2"
                  class="dialog_input"
                  placeholder="用逗号隔开，比如：123,457,457"
                  v-model="arraystr.area_code_arr"
                >
                </el-input>
              </el-form-item>
              <el-form-item
                label="隐藏大厅列表"
                :label-width="formLabelWidth"
                v-show="action.type == 'edit'"
              >
                <el-input
                  type="textarea"
                  :rows="2"
                  class="dialog_input"
                  placeholder="用逗号隔开，比如：123,457,457"
                  v-model="arraystr.hide_node_arr"
                  auto-complete="false"
                >
                </el-input>
              </el-form-item>
              <el-form-item
                label="官方信息"
                :label-width="formLabelWidth"
                v-show="action.type == 'edit'"
              >
                <el-input
                  type="textarea"
                  :rows="2"
                  class="dialog_input"
                  v-model="config.official_info"
                  auto-complete="false"
                >
                </el-input>
              </el-form-item>
              <el-form-item
                label="注册配置"
                :label-width="formLabelWidth"
                v-show="action.type == 'edit'"
              >
                device:<el-input
                  v-model="config.reg_config.device"
                  class="dialog_input_small"
                  auto-complete="false"
                ></el-input>
                手机:<el-input
                  v-model="config.reg_config.mobile"
                  class="dialog_input_small"
                  auto-complete="false"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="App状态"
                :label-width="formLabelWidth"
                v-show="action.type == 'edit'"
              >
                状态码:
                <el-input
                  v-model.number="config.app_state.code"
                  class="dialog_input_small"
                  auto-complete="false"
                ></el-input>
                状态说明:
                <el-input
                  v-model="config.app_state.info"
                  class="dialog_input_small"
                  auto-complete="false"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button
                  @click="(dialogFormVisible = false), resetForm('config')"
                  style="margin-left:18%;"
                  >取 消</el-button
                >
                <el-button type="primary" @click="doCommit">确 定</el-button>
              </el-form-item>
            </el-form>
            <!-- <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false, resetForm('config')">取 消</el-button>
              <el-button type="primary" @click="doCommit">确 定</el-button>
            </div> -->
          </el-dialog>
        </template>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      responseData: [],
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      showTab: false,
      action: {
        type: "",
        title: ""
      },
      config: {
        boss_id: "", //y number
        app_id: "", //y number
        channel_code: "", //y number
        account_config: {},
        app_version: {},
        lobby_version: {},
        app_link: "",
        game_link: "",
        lobby_link: "",
        goods_ids: [],
        area_code_arr: [], //string[]支持区号列表
        hide_node_arr: [], //int[] 隐藏大厅节点
        official_info: "", //官方信息
        reg_config: {},
        app_state: {}
      },
      arraystr: {
        goods_ids: "",
        area_code_arr: "",
        hide_node_arr: ""
      },
      dialogFormVisible: false,
      formLabelWidth: "18%"
    };
  },
  methods: {
    Search() {
      var that = this;
      that.responseData = [];
      this.$api.app.configList(this.requestParam).then(res => {
        if (res.data.success === true) {
          that.responseData = res.data.data.list;
          that.page.total = that.responseData.length;
          const start = (that.page.current_page - 1) * that.page.page_size;
          const end =
            that.page.total > that.page.current_page * that.page.page_size
              ? that.page.current_page * that.page.page_size
              : that.responseData.length;
          that.page.list = that.responseData.slice(start, end);
        } else {
          this.$message({
            showClose: true,
            message: "获取渠道配置列表失败",
            type: "error"
          });
        }
      });
    },
    resetForm() {
      this.config = {
        boss_id: "",
        app_id: "",
        channel_code: "",
        account_config: {},
        app_version: {},
        lobby_version: {},
        app_link: "",
        game_link: "",
        lobby_link: "",
        goods_ids: [],
        area_code_arr: [],
        hide_node_arr: [],
        official_info: "",
        reg_config: {
          // device: "",
          // mobile: ""
        },
        app_state: {
          // code: "",
          // info: ""
        }
      };
      this.arraystr.goods_ids = "";
      this.arraystr.area_code_arr = "";
      this.arraystr.hide_node_arr = "";
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    handleEdit(index) {
      this.action.type = "edit";
      this.action.title = "配置修改";
      this.config = this.page.list[index];
      this.config.reg_config = {
        device: "",
        mobile: ""
      };
      this.config.app_state = {
        code: "",
        info: ""
      };
    },
    doCommit() {
      let that = this;
      if (this.action.type == "edit") {
        that.config.goods_ids = that.arraystr.goods_ids
          .replace(/\s/g, "")
          .replace(/，/g, ",")
          .split(",");
        that.config.area_code_arr = that.arraystr.area_code_arr
          .replace(/\s/g, "")
          .replace(/，/g, ",")
          .split(",");
        that.config.hide_node_arr = that.arraystr.hide_node_arr
          .replace(/\s/g, "")
          .replace(/，/g, ",")
          .split(",");
        this.$api.app.updateChannelConfig(this.config).then(res => {
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: "修改成功！",
              type: "success"
            });
            that.dialogFormVisible = false;
            that.resetForm();
            that.Search();
          } else {
            this.$message({
              showClose: true,
              message: "修改失败",
              type: "error"
            });
          }
        });
      } else if (this.action.type == "add") {
        this.$api.app
          .addChannelConfig({
            app_id: this.config.app_id,
            boss_id: this.config.boss_id,
            channel_code: this.config.channel_code
          })
          .then(res => {
            if (res.data.success === true) {
              this.$message({
                showClose: true,
                message: "新增成功！",
                type: "success"
              });
              that.dialogFormVisible = false;
              that.resetForm();
              that.Search();
            } else {
              this.$message({
                showClose: true,
                message: "新增失败",
                type: "error"
              });
            }
          });
        that.Search();
      }
    },
    addConfig() {
      this.dialogFormVisible = true;
      this.action.type = "add";
      this.action.title = "新增配置";
      this.resetForm();
    },
    // closeTab(that) {
    //   if (that != "undefined" && that != null) {
    //     that.showTab = false;
    //     that.$refs.node.resetFields();
    //   }
    //   this.$refs.node.resetFields();
    //   this.showTab = false;
    // },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.page.list = this.responseData.slice(
        (this.page.current_page - 1) * this.page.page_size,
        this.responseData.length > this.page.current_page * this.page.page_size
          ? this.page.current_page * this.page.page_size
          : this.responseData.length
      );
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    }
  },
  mounted: function() {
    this.Search();
  }
};
</script>

<style lang="less" scoped>
.dialog_input {
  width: 70%;
}
.dialog_input_small {
  width: 20%;
  margin: 0 20px;
}
</style>



